.header-component-48fd4939{& {
  height: 146px;
  padding: 12px 0 0;

  .contacts {
    float: right;
    width: 265px;
    background: url('/imgs/phone.png') no-repeat 0 0;
    padding-left: 55px;
    padding-right: 18px;
  }

  .phone {
    font-size: 22px;
    color: #d90000;
    font-weight: 300;
    margin-bottom: 10px;
  }

  .callBack {
    font-weight: 300;
    font-size: 16px;
    color: #3d3e3d;
  }

  .email {
    color: #d90000;
    line-height: 35px;
    font-size: 17px;
    font-weight: 300;
  }

  .menu {
    width: 100%;
    float: left;
    border-top: 1px solid #ededed;

    ul {
      padding: 0 5px;
      float: left;

      li {
        display: inline-block;
        float: left;
        padding: 0 17px;

        &:hover {
          a {
            text-decoration: none;
            border-color: #d90000;
          }
        }

        a {
          text-align: center;
          display: block;
          color: #363636;
          font-weight: 600;
          text-transform: uppercase;
          padding: 18px 0 0;
          border-bottom: 4px solid #fff;
          height: 56px;
          font-size: 12px;

          &.active {
            text-decoration: none;
            border-color: #d90000;
          }

        }
      }
    }
  }
}}.products-catalog-997af672{& {
  padding: 65px 20px 0;
  background: url('/imgs/catalogBg.png') repeat center 0;
  text-align: center;
  float: left;
  width: 100%;
  @include clearfix;

  .catalogHeader {
    font-size: 25px;
    font-weight: bold;
    color: #585c62;
    text-transform: uppercase;
    margin-bottom: 40px;
  }

  .catalogInner {
    @include clearfix;
  }

  .catalogItem {
    display: inline-block;
    float: left;
    width: 225px;
    margin: 0 20px 20px 0;

    &:last-child {
      margin-right: 0;
    }

    a {
      background: #fff;
      border: 1px solid #e6ecf0;
      border-radius: 3px;
      box-shadow: 0 0 3px #eee;
      padding: 25px 15px;
      display: block;
      font-weight: 300;
      font-size: 20px;
      color: #585c62;
      height: 215px;
      line-height: 24px;

      &:hover {
        border-color: #fff;
        outline: 3px solid #da0e0e;
        text-decoration: none;
        color: #da0e0e;
      }
    }

    img {
      height: 125px;
    }
  }
}}